@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.footer {
  width: 100%;
  padding: 6rem 0;
  background-color: rgba(0, 0, 0, 0.8);
}

.footer-container {
  max-width: 1140px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
}

.footer .left {
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

.footer .left p {
  font-family: 'Playfair Display', serif;
  letter-spacing: 0.05em;
}

.footer .right {
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

.footer h4 {
  padding: 0 0 1rem;
}

.footer p {
  padding: 0.5rem 0;
  text-justify: inter-word;
  line-height: 1.3;
}

.footer .location {
  display: flex;
  align-items: center;
}

.footer .location p {
  padding-top: 0;
}

.footer .social {
  padding-top: 1rem;
}

@media screen and (max-width: 640px) {
  .footer-container {
    grid-template-columns: 1fr;
  }
}

#video {
  position: fixed;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.hero {
  height: 100vh;
  width: 100%;
  object-fit: contain;
}

.main-img {
  position: fixed;
  width: 100%;
  height: 100%;
  background: url(/law-company/static/media/main.deec69c4.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: -2;
}

.main-img:before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.438);
  content: '';
}

.hero .content {
  text-align: center;
  position: absolute;
  margin: auto;
  width: 100%;
  height: 100%;
  top: 40vh;
}

.hero .content h1 {
  margin-bottom: 1.4rem;
  padding: 0 1.1rem;
}

.hero .content p {
  margin-bottom: 4.5rem;
  padding: 0 1.1rem;
  font-family: 'Playfair Display', serif;
  font-size: 1.5rem;
  font-weight: 200;
  line-height: 1.3;
  letter-spacing: 0.06em;
  text-transform: uppercase;
}

.hero .btn {
  margin: 1rem 0.2rem;
}

@media screen and (max-width: 992px) {
  .hero .content h1 {
    font-size: 3rem;
  }

  .hero .content p {
    font-size: 1.4rem;
    margin-bottom: 4rem;
  }
}

@media screen and (max-width: 678px) {
  .hero .content h1 {
    font-size: 2.4rem;
  }

  .hero .content p {
    font-size: 1.2rem;
    margin-bottom: 3rem;
    font-weight: 300;
  }
}
@media screen and (max-width: 678px) {
  .hero .content h1 {
    font-size: 2rem;
  }

  .hero .content p {
    font-size: 1rem;
    margin-bottom: 3rem;
  }
  .hero .btn {
    padding: 10px 30px;
  }
}

.header {
  font-family: 'Outfit';
  letter-spacing: 0.03em;
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  z-index: 10;
  background-color: #0000000d;
}

.header-dark {
  background-color: rgba(0, 0, 0, 0.85);
  transition: 0.5s;
}

#logo-company {
  font-family: 'Montserrat', sans-serif;
  font-size: 2.5rem;
  font-weight: 600;
}

.nav-menu {
  display: flex;
}

.nav-menu li {
  padding: 0 1rem;
}

.nav-menu a {
  font-size: 1.18rem;
  letter-spacing: 0.04em;
  font-weight: 500;
  text-transform: uppercase;
}

.hamburger {
  display: none;
}

@media screen and (max-width: 1240px) {
  .hamburger {
    display: block;
    cursor: pointer;
  }

  #logo-company {
    font-size: 2rem;
  }

  .nav-menu {
    position: absolute;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 0;
    left: -100%;
    text-align: center;
    width: 100%;
    height: 100vh;
    transition: 0.3s;
    z-index: -1;
    background-color: rgba(0, 0, 0, 0.9);
  }

  .nav-menu.active {
    left: 0;
  }

  .nav-menu li {
    padding: 1rem 0;
  }
  .nav-menu a {
    font-family: 'Playfair Display', serif;
    letter-spacing: 0.05rem;
    font-size: 2rem;
  }
}

.hero-img {
  background-color: rgba(0, 0, 0, 0.3);
  height: 50vh;
  width: 100%;
  position: relative;
}

.hero-img::before {
  content: '';
  background-image: url(/law-company/static/media/unsplash-min.b1502ae0.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.hero-img .heading {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.heading_subtitle {
  margin-top: 2rem;
  padding: 0 1rem;
  font-size: 1.4rem;
  text-align: center;
}
.pricing {
  max-width: 1440px;
  margin: 5rem auto 3rem auto;
  display: grid;
  grid-template-columns: repeat(2, minmax(320px, 1fr));
  grid-auto-rows: auto;
  height: 100%;
}

.pricing-left {
  padding: 1rem 4rem;
}

.pricing-left h2 {
  margin-bottom: 2rem;
}

.pricing-left p {
  margin: 1.2rem 0;
  line-height: 1.3;
}

.pricing-btn {
  margin-top: 2rem;
}

.pricing-right {
  width: 100%;
  text-align: center;
}

.pricing-right .img-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 1fr;
  position: relative;
  align-items: flex-start;
  justify-items: center;
}

.img-container .top {
  grid-column: 1 / span 8;
  grid-row: 1;
  z-index: 6;
}

.img-container .bottom {
  grid-column: 3 / -1;
  grid-row: 1;
  z-index: 5;
  margin-top: 11%;
}

.foto1 {
  max-width: 60%;
  border: 1px solid #959595;
}

.foto2 {
  max-width: 60%;
  border: 1px solid #333333;
}

@media screen and (max-width: 1140px) {
  .pricing {
    grid-template-columns: 1fr;
  }

  .pricing-left {
    margin-bottom: 3rem;
  }

  .pricing-right img {
    max-width: 80%;
  }
}

@media screen and (max-width: 678px) {
  .pricing-left {
    padding: 1rem 2rem;
  }
}

@media screen and (max-width: 458px) {
  .pricing-left {
    padding: 1rem 1.3rem;
  }
}

.training {
  width: 100%;
  padding: 6rem 1rem;
  background-color: #020202;
}

.card-container {
  max-width: 1140px;
  display: grid;
  grid-template-columns: repeat(3, minmax(330px, 1fr));
  grid-gap: 40px;
  margin: 0 auto;
}

.card {
  text-align: center;
  border: 1px solid #ffffff9a;
  padding: 1rem;
  display: flex;
  flex-direction: column;
}

.card:hover {
  background-color: rgb(22, 31, 55);
}

.card-title {
  letter-spacing: 0.07em;
  font-weight: 400;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
}

.card p {
  line-height: 1.3;
  font-weight: 300;
  padding: 0.5rem 0 0.5rem 1.5rem;
  text-align: left;
  position: relative;
}

.card p::before {
  position: absolute;
  content: '-';
  left: 0;
  top: 0.5rem;
  color: #ffffff;
}

.card p:last-of-type {
  margin-bottom: 3rem;
}

.card .btn {
  margin: auto auto 2rem;
  display: block;
  width: 80%;
  color: #ffffff;
}

.card .bar {
  border-bottom: 1px solid #eeeeee;
  width: 17%;
  margin: 1.5rem auto;
  display: block;
}

@media screen and (max-width: 1220px) {
  .card-container {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: row;
  }

  .card p {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 820px) {
  .card-container {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 640px) {
  .card-container {
    grid-template-columns: 1fr;
    margin: 0 auto;
  }
}

@media screen and (max-width: 440px) {
  .card p {
    font-size: 1rem;
  }
}

.form {
  display: flex;
  flex-direction: column;
  padding: 5rem 1rem 1rem 1rem;
  margin: auto;
  max-width: 600px;
}

.form label {
  display: block;
  color: #ffffff;
  letter-spacing: 0.08em;
}

.form input,
.form textarea {
  display: block;
  width: 100%;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  padding: 10px;
  font-size: 1.2rem;
  background-color: #ffffff23;
  outline: none;
  border: 1px solid #454545;
  font-family: 'Outfit', sans-serif;
  color: #f4f4f4;
}

.input-error {
  margin: -0.8rem auto 1.1rem;
  font-size: 1.1rem;
  color: rgb(201, 35, 35);
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Playfair Display', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000000;
  font-size: 16px;
}

h1,
h2,
h4,
p,
a {
  color: #ffffff;
}

h1 {
  font-size: 3.4rem;
  line-height: 1.1;
  letter-spacing: 0.04em;
}

h2 {
  font-size: 2.8rem;
  line-height: 1.1;
  letter-spacing: 0.04em;
}

h4 {
  font-size: 1.4rem;
  letter-spacing: 0.04em;
}

p {
  font-size: 1.15rem;
  letter-spacing: 0.02em;
  line-height: 1.1;
  font-family: 'Montserrat', sans-serif;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.btn {
  padding: 12px 32px;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.2rem;
  text-transform: uppercase;
  background-color: transparent;
  color: #ffffff;
  border: 1px solid #ffffff;
  cursor: pointer;
}

.btn-light {
  background-color: rgba(255, 255, 255, 0.2);
}

.btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

@media screen and (max-width: 640px) {
  h1 {
    font-size: 2rem;
  }
  .btn {
    font-size: 1.1rem;
  }
  p {
    font-size: 1.1rem;
  }
  input::-webkit-input-placeholder {
    font-size: 1.15rem;
  }
  input:-ms-input-placeholder {
    font-size: 1.15rem;
  }
  input::placeholder {
    font-size: 1.15rem;
  }
  textarea::-webkit-input-placeholder {
    font-size: 1.15rem;
  }
  textarea:-ms-input-placeholder {
    font-size: 1.15rem;
  }
  textarea::placeholder {
    font-size: 1.15rem;
  }
}



